.display2{
  font-weight: 600;
  font-size: 24px;
  line-height: 132%;
}

.display3{
  font-weight: 600;
  font-size: 18px;
  line-height: 111%;
}

.article__text ul,
.mlist{
  font-size: 16px;
  line-height: 112%;
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: $black;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}

@media screen and (max-width: 992px){
  .display2{
    font-size: 20px;
  }
}

@media screen and (max-width: 768px){
  .display2{
    font-size: 18px;
    line-height: 25px;
  }

  .display3{
    font-size: 14px;
  }

  .mlist{
    font-size: 14px;
    line-height: 133%;
    li{
      &:not(:last-child){
        margin-bottom: 10px;
      }
      &:before{
        transform: translateY(6px);
      }
    }
  }
}
